<template>
  <div class="cell-container my-background-color-secondary my-view-shadow p-0">
      <div class="row m-0">
        <div class="col-auto m-1">
            <i class="icon-member"></i>
        </div>
        <div class="col m-0 align-self-center">
            {{ info.name }}
        </div>
        <div class="col-auto m-0 align-self-center">
            {{ info.gender }}
        </div>
        <div class="col m-0 align-self-center">
            32歲
        </div>
        <div class="col-auto m-0 align-self-center">
            {{ info.birthday }}
        </div>
        <div class="col m-0 align-self-center">
            {{ info.height }}cm / {{ info.weight }}kg
        </div>
        <div class="col m-0 align-self-center">
            BMI: {{ bmi }}
        </div>
        <div class="col m-0 align-self-center">
            體指率: {{ info.pbf }}%
        </div>
        <div class="col-auto trash-block my-accent-color-primary d-flex rounded-end">
            <button class="btn icon-trash" @click="onDelete($event)"></button>
        </div>
      </div>
  </div>
</template>

<script>
export default {
    props: {
        info: Object
    },
    computed: {
        bmi: function() {
            let original = (this.info.weight) / Math.pow((this.info.height / 100), 2);
            let rounded = Math.round(original * 10) / 10;
            return rounded
        }
    },
    methods: {
        onDelete: function(e) {
            console.log('Delete!!');
            e.stopPropagation()
        }
    }
}
</script>

